html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

h1,
body {
  font-family: "League Spartan", sans-serif;
  font-weight: 700;
  font-size: 1.6rem;
}

button {
  border: none;
  background-color: transparent;
}

fieldset {
  border: none;
}
